.flex-container{
    padding-top: 30px;
    display: flex;
    flex-direction: row;
    padding-left: 60px;
}

.back_btn{
    width: 130px;
    height: 45px;
    padding: 10px;
    font-size: 20px;
    font-family: sans-serif;
    font-weight: 300;
    background-color: #007b5e;
    color: white;
    text-decoration: none;
    margin-left: 80px;
    margin-top: 30px;
    margin-bottom: 20px;
}

.hr{
    width: 84%;
    justify-content: left;
    margin-left: 80px;
}

.hr-inLabel{
    width: 70% ;
    margin-left: 35px;
}

.details_image{
    display: flex;
    margin-top: 30px;
    width: 350px;
    justify-content: flex-start;    
    align-items: center;
    padding-left: 20px;
    padding-top: 5px;
    flex: 30%;
}

.book_image{
    width: 330px;
    height: 420px;
}

.book_details{
    margin-top: 50px;
    float: center;
    flex: 70%;
}

.rows{
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 15px;
    display: flex;
}

.label{
    padding: 10px;
    background-color: #333;
    color: white;
    font-weight: 500;
    margin-left: 20px;
    width: 120px;
}

.book_name{
    margin-left: 20px;
    font-weight: 30;
    font-size: 25px;
}

.author{
    margin-left: 20px;
    font-weight: 30;
    font-size: 25px;
}

.year_of_release{
    margin-left: 20px;
    font-weight: 30;
    font-size: 25px;
}

.publication{
    margin-left: 20px;
    font-weight: 30;
    font-size: 25px;
}

@media (max-width: 800px) {
    .flex-container {
      flex-direction: column;
      padding-left: 0px;  
      padding-top: 0px;
    }

    .book_image{
        width: 230px;
        height: 320px;
    }

    .back_btn{
        width: 130px;
        height: 45px;
        padding: 10px;
        font-size: 18px;
        margin-left: 20px;
        margin-top: 60px;
    }

    .hr{
        width: 88%;
        align-self: flex-start;
        display: inline-flex;
        margin-left: 20px;
    }
    
    .hr-inLabel{
        width: 70% ;
        margin-left: 35px;
    }   

    .rows{
        padding-left: 0px;
    }
  }